* {
  margin: 0;
  padding: 0; }

html {
  background-color: #622251; }

.grid-item {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: antiquewhite; }

html {
  background-color: #622251; }

p {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  color: #ffd691; }

h1 {
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  color: #ffd691; }

#grid-container {
  display: grid;
  justify-content: center;
  justify-items: flex-start;
  grid-template-columns: auto auto auto auto;
  grid-gap: 2vw;
  align-content: flex-start;
  margin: 3.5vw 0 4vw 0; }

.grid-item {
  height: 20vw;
  width: 20vw;
  background-color: rgba(255, 255, 255, 0.13);
  border-radius: 10px;
  box-shadow: 0px 0px 31px -2px rgba(0, 0, 0, 0.35); }

#fuckTheDealer,
#hetspong,
#jagharaldrig,
#pekleken,
#ringoffire,
#overunder,
#bussen {
  height: 7.8vw; }

#button {
  position: absolute;
  top: 2vw;
  left: 2vw;
  z-index: 100;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center; }
  #button img {
    margin-right: 0.2vw;
    width: 10px;
    height: 10px; }
  #button p {
    font-size: calc((0.8px + 0.05335vmin) * 12px); }

.slideContainer {
  z-index: -1;
  display: flex;
  justify-content: center; }

.carousel.carousel-slider .control-arrow {
  padding: 2vw !important; }

.carousel .control-dots .dot {
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0) !important;
  background: rgba(0, 0, 0, 0) !important;
  border: 1px solid #ffd691;
  opacity: 1 !important; }
  .carousel .control-dots .dot.selected {
    background: #ffd691 !important; }

.carousel .slide {
  text-align: left !important;
  background-color: rgba(0, 0, 0, 0) !important;
  padding: 0 10vw 0 10vw;
  width: 100vw;
  height: calc(100vh - 4vw);
  display: flex;
  justify-content: center; }

#appContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

#logo {
  width: 20vw;
  margin-top: 3vw; }

@media screen and (min-width: 1100px) {
  #grid-container {
    grid-template-columns: auto auto auto auto; }
  p {
    font-size: 20px; }
  .carousel .slide {
    padding: 0 30vw 0 30vw; } }

@media screen and (max-width: 1100px) {
  #grid-container {
    grid-template-columns: auto auto auto;
    grid-gap: 3vw; }
  .grid-item {
    height: 25vw;
    width: 25vw; }
  p {
    font-size: 18px; }
  .carousel .slide {
    padding: 0 15vw 0 15vw; } }

@media screen and (max-width: 750px) {
  #fuckTheDealer,
  #hetspong,
  #jagharaldrig,
  #pekleken,
  #ringoffire,
  #overunder,
  #bussen {
    height: 16.2vw; }
  #grid-container {
    grid-template-columns: auto auto;
    grid-gap: 5vw;
    margin-top: 5vw; }
  .grid-item {
    height: 42vw;
    width: 42vw; }
  p {
    font-size: 16px; }
  .carousel .slide {
    padding: 0 10vw 0 10vw; } }

@media screen and (max-width: 400px) {
  p {
    font-size: 14px; }
  .control-dots {
    margin-bottom: 10vw; } }

